import config from "../config";
import { lazyRequest } from "../utils/request";
import { onBFCacheRestore } from "../utils/utils";
export default function error() {
  const primaryConsoleError = window.console.error;
  window.console.error = (...args) => {
    primaryConsoleError.apply(this, args);
    lazyRequest({
      type: "error",
      subType: "console-error",
      errData: args,
    });
  };
  // æè·èµæºå è½½å¤±è´¥éè¯¯ js css img...
  window.addEventListener(
    "error",
    (e) => {
      const target = e.target;
      if (!target) return;
      // å¤æ­æ¯å¦æ¯èµæº
      const url = target.src || target.href;
      if (url) {
        lazyRequest({
          type: "error",
          subType: "resource",
          url,
          startTime: e.timeStamp,
          html: target.outerHTML,
          resourceType: target.tagName,
          paths: e.path.map((item) => item.tagName).filter(Boolean),
        });
      }
    },
    true
  );

  window.onerror = (msg, url, line, column, error) => {
    lazyRequest({
      type: "error",
      subType: "js",
      msg,
      url,
      line,
      column,
      error,
    });
  };
  // çå¬promiseç­å¼æ­¥æ¥éï¼ç¼ºç¹æ¯æ¿ä¸å°åæ°æ®
  window.addEventListener("unhandledrejection", (e) => {
    debugger;
    lazyRequest({
      type: "error",
      subType: "promise",
      reason: e.reason?.stack,
    });
  });

  if (config.vue?.Vue) {
    config.vue.Vue.config.errorHandler = (err, vm, info) => {
      console.error(err);
      lazyReportCache({
        subType: "vue",
        type: "error",
        info,
        error: err.stack,
      });
    };
  }
  if (config.react?.React) {
    //
  }
  onBFCacheRestore(() => {
    error();
  });
}
