let timer = null;
export function lazyRequest(item, timeout = 3000) {
  const i = { ...item };
  i.pageURL = getPageURL();
  // performance.now(),ä» page load çæ¶åä»é¶å¼å§è®¡æ°ã
  if (!i.startTime) {
    i.startTime = performance.now();
  }
  i.id = getUuid();
  i.date = +new Date();
  addData(item);
  clearTimeout(timer);
  timer = setTimeout(() => {
    const data = getData();
    if (data.length) {
      request(data);
      clearData();
    }
  }, timeout);
}
